				object2.position.x *= -1.0;
				object2.position.z *= -1.0;

				// this.intersectsScene.children[INSTANCES*5+i] = object2 ;
				dummy2.push(object2);
			// 	object2.cursor = "pointer";

			// 	object2.on('click',
			// 		function(ev) {
			// 		 myThis.goEventClicked(ev, myThis.meshArr[5],5);
			// }

			// 	);
