showTable = () => {
  	const width = document.documentElement.clientWidth;
    	const height = document.documentElement.clientHeight;
  	if (firstTime){
  		this.interval = setInterval(() => this.updateArrow(), 300);
  		
  		 if(isMobile){
	    	if ((width/height)<1){
	    		if (window.innerWidth < 420) {
		    		this.setState({
				      	showTableRow:"none",
		    			seTop:"90px"
				    });
		    	
	    	}
	    }
	    	if ((width/height)>1){
				this.setState({
			      	showTableRow:"none",
	    			seTop:"90px",
	    			showDesc:"none",
	    			tableFontSize:"9px",
	    			marqueeFontSize:"32px"
			    });
	    	}
	    }
	    firstTime = false;
  	}
  	if (tableShow){
  		this.setState({
	      tableDisplay: "none",
	      petitDisplay:"none"
	    });
	    tableShow = !tableShow;
	    clearInterval(this.interval);
	    firstTime = true;
	    arrowSymbols = "";
  	}else{
  		// if(!creditShow){
  			if (this.state.marqueeTxt == "ϕth Daddy Master Class ⟿ Try"){
  				var tempCreditShow = this.state.creditDisplay;
  				if (creditShow){
  					tempCreditShow = "none";
  					creditShow = false;
  				}
  				if(isMobile){
			    	if ((width/height)<1){
				    		this.setState({
						      	showDesc:"inherit"
						    });
			    	}
			    }
	  			this.setState({
			      	tableDisplay: "inherit",
		      		petitDisplay:"none",
		      		creditDisplay:tempCreditShow
			    });
			    tableShow = !tableShow;
	  		}
  		// }
  		
  	}
  }
