  handleWindowResize = () => {
    // const width = this.el.clientWidth;
    // const height = this.el.clientHeight;
	const width = document.documentElement.clientWidth;
    const height = document.documentElement.clientHeight;
    this.setState({
      wWidth:document.documentElement.clientWidth,
      wHeight:document.documentElement.clientHeight
    });
    if (isMobile){
    	INSTANCES = 64;
    	gltfScale = 1000;
    }else if(isBrowser){
    	INSTANCES = 128;
    	gltfScale = 1500;
    }


    // this.sceneSetup();
    var elmnt = document.getElementById("marquee");
    if (elmnt!= undefined){
    	var tableHeight = height - (elmnt.offsetHeight*2) - 6;
    var tableTop = elmnt.offsetHeight + 6;
    var showDesc = "inherit";
    var tableFontSize = "";
    var marqueeFontSize = "";
    if (isMobile){
    	if ((width / height) > 1){
	    	showDesc = "none";
	    	tableFontSize = "9px";
	    	marqueeFontSize = "32px";
	    	// tableHeight = height - (35*2) - 6;
	    }
    }
    
    this.setState({
	      tableHeight: tableHeight + "px",
	      tableTop: tableTop +"px",
	      showDesc: showDesc,
	      tableFontSize: tableFontSize,
	      marqueeFontSize:marqueeFontSize
	    });
    }

        this.camera.aspect = width / height;
    // if(isBrowser){
    	this.renderer.setSize(width, height);
    	this.camera.updateProjectionMatrix();
    	
    // }
  };

